<template>
  <aside class="sidebar">
    <div class="menu">
      <h2 class="title">Pictures Chat</h2>
      <div
        v-for="(msg,index) in chatMessages"
        :key="'chat_message_'+index"
        class="field"
      >
        <div class="tag is-primary">
          {{ msg }}
        </div>
      </div>
      <div class="field is-horizontal is-grouped">
        <b-input type="text" v-model="newMsgText" />
        <b-button icon-right="paper-plane" type="is-primary" @click="sendNewMessage" />
      </div>
    </div>
  </aside>
</template>
<script>
import { mapState } from 'vuex'

export default {
  name: 'SidebarChat',
  data () {
    return {
      newMsgText: ''
    }
  },
  computed: mapState({
    chatMessages: state => state.chatMessages
  }),
  mounted () {
    this.$store.dispatch('connectWebSocket')
  },
  methods: {
    sendNewMessage () {
      this.$store.dispatch('sendWsMessage', this.newMsgText)
      this.newMsgText = ''
    }
  }
}
</script>
